.tips {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #cccccc;

  .item {
  	height: 100rpx;
  	margin-top: 50rpx;
  	display: block;
  	width: 100%;
  	border-top: 1rpx solid black;
  	border-bottom: 1rpx solid black;
  	background-color: white;
  	font-size: 40rpx;

  	text {
  		display: block;
  		width: 40%;
  		text-align: center;
  		float: left;
  		height: 100rpx;
  		line-height: 100rpx;
  	}

  	button {
  		display: block;
  		border-radius: 0;
  		width: 30%;
  		height: 60rpx;
  		line-height: 60rpx;
  		margin-top: 20rpx;
  		margin-right: 40rpx;
  	}
  }

}
